            .startprofile {
                color: khaki;
            }
            
            .logoandQ {
                padding-bottom: 20px;
                .qouts {
                    display: flex;
                    margin-top: 30px;
                    img {
                        max-height: 200px;
                        @media (max-width: 767.98px) {
                            width: 140px;
                            height: 190px;
                        }
                    }
                    .textsaied {
                        margin-left: 10px;
                        margin-top: 10px;
                        h3 {
                            font-size: 1.4rem;
                            @media (max-width: 991.98px) {
                                font-size: 1rem;
                            }
                            @media (max-width: 767.98px) {
                                font-size: 1rem;
                            }
                        }
                        h6 {
                            font-size: .9rem;
                            color: #999;
                            @media (max-width: 991.98px) {
                                font-size: .8rem;
                            }
                            @media (max-width: 767.98px) {
                                font-size: .7rem;
                            }
                        }
                        p {
                            font-size: 1.1rem;
                            color: #999;
                            font-family: cursive;
                            font-style: italic;
                            @media (max-width: 991.98px) {
                                font-size: 1.1rem;
                            }
                            @media (max-width: 767.98px) {
                                font-size: 1rem;
                            }
                            .fa-quote-left,
                            .fa-quote-right {
                                color: #ffb14b;
                            }
                        }
                        h5 {
                            margin-left: 60px;
                            font-size: .9rem;
                            color: #999;
                            max-width: 70%;
                            font-family: cursive;
                            font-style: italic;
                            @media (max-width: 991.98px) {
                                font-size: .9rem;
                            }
                            @media (max-width: 767.98px) {
                                font-size: .8rem;
                            }
                        }
                    }
                }
                .logoo {
                    text-align: right;
                }
            }
            
            .sidepar-icons {
                width: 3rem;
                height: 100vh;
                position: fixed;
                .nav_links {
                    list-style: none;
                    li {
                        margin: 1.2rem auto;
                        font-size: 1.1rem;
                        width: 1.7rem;
                        @media (max-width: 767.98px) {
                            font-size: 1rem;
                        }
                        &.active {
                            background-color: #f9f9f9;
                            border-radius: 10px;
                        }
                        a {
                            color: #9d9da0;
                        }
                    }
                }
            }
            
            h6 {
                color: #999;
                font-size: 1rem;
                font-weight: 600;
            }
            
            input,
            select,
            textarea {
                background: #eaeaf4 !important;
                &:focus {
                    box-shadow: none !important;
                }
            }
            
            .pagecontent {
                margin-left: 3.2rem;
                // @media (max-width: 767.98px) {
                //     margin-left: 2rem;
                // }
                width: 100%;
                .content_wrapper {
                    margin: 0px;
                    @media (min-width: 1199.98px) {
                        margin-right: 3rem;
                    }
                    #intro {
                        padding-top: 20px;
                        padding-bottom: 20px;
                        .infocontainer {
                            width: 70%;
                            margin: 1px auto;
                        }
                        .addpho {
                            color: #9d9da0;
                            display: block;
                            border: 0;
                            margin-top: 33px;
                            @media (max-width: 767.98px) {
                                font-size: .9rem;
                            }
                        }
                        .address {
                            textarea {}
                        }
                    }
                    .lighcontainer {
                        padding: 0 2px;
                        justify-content: space-between;
                        align-items: center;
                        background-color: #fff;
                        border-radius: 20px;
                        border: 1px solid #999;
                        border-bottom-left-radius: 0;
                        border-bottom-right-radius: 0;
                        .table-responsive {
                            .table {
                                margin-bottom: 0px;
                            }
                            .table tbody+tbody {
                                border-top-color: transparent !important;
                            }
                            th {
                                border-left: 1px solid #999;
                                border-right: 1px solid #999;
                                &:first-of-type,
                                &:last-of-type {
                                    border-left: 0px solid #999;
                                    border-right: 0px solid #999;
                                }
                            }
                            td {
                                border-left: 1px solid #999;
                                border-right: 1px solid #999;
                                &:first-of-type,
                                &:last-of-type {
                                    border-left: 0px solid #999;
                                    border-right: 0px solid #999;
                                }
                            }
                        }
                        .delivery {
                            border-top: 1px solid #999;
                            padding-top: 10px;
                            padding-bottom: 10px;
                            .deld {
                                position: relative;
                                font-weight: bold;
                            }
                            .deld:after {
                                position: absolute;
                                content: '';
                                color: #999;
                                background-color: #999;
                                width: 1px;
                                top: -10px;
                                right: 11%;
                                height: 44px;
                            }
                        }
                        .customer-price {
                            padding-top: 10px;
                            padding-bottom: 10px;
                            span {
                                @media (max-width: 767.98px) {
                                    font-size: .9rem;
                                }
                            }
                            button {
                                background-color: #f3f3fb;
                                margin-left: 10px;
                                margin-right: 10px;
                                font-size: .9rem;
                                @media (max-width: 767.98px) {
                                    margin-left: 0px;
                                    margin-right: 0px;
                                }
                            }
                        }
                        .Tptalprofit {
                            @media (max-width: 767.98px) {
                                font-size: .9rem;
                            }
                            .btn-successs {
                                background-color: #2ee37c;
                                margin-left: 10px;
                                margin-right: 10px;
                                color: #FFF;
                                @media (max-width: 767.98px) {
                                    margin-left: 1px;
                                    margin-right: 1px;
                                    font-size: .9rem;
                                }
                            }
                        }
                    }
                    .nored {
                        border-radius: 0;
                        border: 0;
                        padding-bottom: 20px;
                    }
                }
            }
            
            #myDropdown {
                max-width: 100%;
            }
            
            .dd-selected {
                height: calc(1.5em + .75rem + 2px) !important;
                font-weight: 400 !important;
                color: #495057 !important;
                background-color: #eaeaf4 !important;
                border-radius: .25rem !important;
                transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out !important;
            }
            
            .dd-options {
                max-width: 13.4rem;
                margin-top: 5px;
                margin-bottom: 5px;
            }
            
            .dd-option-image,
            .dd-selected-image {
                max-width: 34px !important;
                float: inherit !important;
                margin-right: 30px !important;
            }
            
            .dd-selected {
                padding: 0px 10px !important;
            }
            
            .dd-select {
                max-width: 100%;
            }
            
            .Quantityy {
                span {
                    cursor: pointer;
                }
                .minus,
                .plus {
                    width: 20px;
                    height: 20px;
                    background: #222;
                    line-height: 20px;
                    font-size: 25px;
                    border-radius: 50%;
                    display: inline-block;
                    vertical-align: middle;
                    text-align: center;
                    color: #FFF;
                    font-weight: bolder;
                }
                input {
                    height: 34px;
                    width: 40px;
                    text-align: center;
                    font-size: 1rem;
                    border: 0px solid #ddd;
                    border-radius: 4px;
                    display: inline-block;
                    vertical-align: middle;
                    background-color: transparent !important;
                }
            }
            
            .buttonsContainer {
                text-align: right;
                button {
                    border-radius: 20px;
                    font-size: .9rem;
                    font-weight: bold;
                    width: 8rem;
                    &.next {
                        background-color: #ffb14b;
                    }
                }
            }
            
            .cityinpu {
                @media (max-width: 767.98px) {
                    margin-top: 20px;
                }
            }